<template>
    <div class="z-10 flex-none fixed bottom-0" @mouseover="hideBoard(15000)" @mouseleave="hideBoard(5000)">
        <transition enter-active-class="animated fadeInLeft" leave-active-class="animated fadeOutLeft">
            <div v-show="showBoard" id="notifications" class="ml-8 mb-2 pl-3 pr-2 pb-1 shadow-lg w-300px h-64 rounded-md bg-notifications overflow-y-auto overflow-x-hidden scrollbar">
                <transition-group enter-active-class="animated fadeIn">
                    <p v-for="(notification,index) in notifications" :key="index+1">[
                        <span class="text-green-500">{{notification.timestamp}}</span>
                        ] {{notification.notification}}</p>
                </transition-group>
            </div>
        </transition>
        <img src="/images/armada-cuy-mini.png" alt="" class="w-10 m-1 cursor-pointer" @click="toggleBoard">
        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
            <i v-show="showBoard" class="fixed fas fa-times-circle cursor-pointer text-transparent hover:text-black" style="left:1rem;bottom:1rem;" @click="hideBoard(0)"></i>
        </transition>
    </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
    name: 'Notifications',
    data(){
        return {
            showBoard:false,
            timer:null
        }
    },
    computed:{
        ...mapState({
            notifications: state=>state.notifications,
            me: state=>state.me,
        })
    },
    watch:{
        notifications(){
            this.showBoard = true
            let element = document.getElementById("notifications") 
            this.$nextTick().then(()=>{
                element.scrollTop = element.scrollHeight - element.clientHeight
            })  
            this.hideBoard(3000)
        }
    },
    created(){
        
    },
    methods:{
        toggleBoard(){
            this.showBoard = !this.showBoard
            if(this.showBoard){
                this.hideBoard(10000)
            }
        },
        hideBoard(time){
            clearTimeout(this.timer)
            this.timer = setTimeout(()=>{
                this.showBoard = false
            },time)            
        }
    }
}
</script>