<template>
    <div class="flex flex-col">
        <vue-simple-context-menu
            :element-id="'lobbyListLeader'"
            :options="optionsLeader"
            :ref="'lobbyControlContextMenuLeader'"
            @option-clicked="optionClicked"
        >
        </vue-simple-context-menu>
        <vue-simple-context-menu
            :element-id="'lobbyListLeaderForFriends'"
            :options="optionsLeaderForFriends"
            :ref="'lobbyControlContextMenuLeaderForFriends'"
            @option-clicked="optionClicked"
        >
        </vue-simple-context-menu>
        <vue-simple-context-menu
            :element-id="'lobbyListGeneral'"
            :options="optionsGeneral"
            :ref="'lobbyControlContextMenuGeneral'"
            @option-clicked="optionClicked"
        >
        </vue-simple-context-menu>
        <div v-show="letsroll" class="flex z-40 flex-none fixed top-0 left-0 w-full h-screen bg-super-dark-blur justify-center items-center">
            <div class="flex bg-cover bg-center w-1/3 h-64 shadow-green-glow " :style="{ backgroundImage: `url('/images/maps/${this.mapSelected}.jpg')`}">
                    <div v-show="showLoader" class="flex flex-col justify-center items-center flex-grow backdrop-blur">
                        <div class="lds-hourglass"></div>
                        <p class="text-shadow">Preparando el Mixton</p>
                    </div>
                    <div v-show="!showLoader" class="flex flex-col justify-center items-center flex-grow">
                        <p class=" text-5xl font-bold text-shadow">Go Go Go!</p>
                        <div class="flex flex-col bg-super-dark-blur w-3/5 h-32 pt-2 items-center">
                            <div class="flex px-5 pt-2 mt-1 ">
                                <div class="w-56 flex justify-center">
                                    <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode="out-in" :duration="200">
                                        <span class="mr-2 " :key="connectCommand">{{connectCommand}}</span>
                                    </transition>
                                </div>
                                <div>
                                    <i v-clipboard="connectCommand" 
                                    class="cursor-pointer fas fa-copy"
                                    v-clipboard:success="clipboardSuccessHandler"></i>
                                </div>                                
                            </div>
                            <button class="bg-green-600 hover:bg-green-500 text-white focus:outline-none my-5 py-2 px-4 cursor-pointer w-40" 
                            @click="gotIn=true" :class="{'disabled':gotIn}">Entrar</button>
                        </div>
                    </div>
                
                
            </div>
            <audio id="letsRoll">
                <source src="/sounds/letsroll.mp3" type="audio/mpeg">
                <source src="/sounds/letsroll.ogg" type="audio/ogg">
                Por alguna razon no se pueden cargar sonidos.
            </audio>
        </div>
        <div class="flex my-3">

          <div class="w-2/4 flex justify-center items-center">
              <p class="text-xl">Espectadores</p>
          </div>
          <div class="w-1/4 flex justify-center items-center">
              <p v-if="lobby.tSide[0]" class=" text-xl">Equipo de {{lobby.tSide[0].name}}</p>
              <p v-else class="text-xl">Equipo 1</p>
          </div>
          <div class="w-1/4 flex justify-center items-center">
                <p v-if="lobby.ctSide[0]" class=" text-xl">Equipo de {{lobby.ctSide[0].name}}</p>
                <p v-else class=" text-xl">Equipo 2</p>
          </div>
        </div>
       <div class="flex ml-3">
        <draggable
            v-model="lobby.spectators"
            id="spectators"
            group="people"
            animation="200" 
            :sort="false"
            @change="log"
            @end="debounceLobbyUpdate"
            class="flex flex-col w-2/4 flex-wrap max-h-spectators h-spectators bg-dark-blur flex-shrink-0 mr-2"
            :move="checkMove"
            >
                <player-item 
                    class="max-w-sm" 
                    v-for="(player,index) in lobby.spectators" 
                    :key="index" 
                    :player="player" 
                    :status="lobby.leader._id == player._id ?'Espectador | Lobby Leader':'Espectador'"
                    side="spec"
                    @contextmenu.native.prevent.stop="lobbyListRightClick($event,player)"
                ></player-item>
        </draggable>
        <div class="flex flex-col flex-grow">
            <div class="flex">
                <draggable 
                    v-model="lobby.tSide"
                    id="terrorists"
                    group="people"
                    animation="200" 
                    @change="log"
                    @end="debounceLobbyUpdate"
                    class="flex flex-col w-1/2 flex-wrap h-sides bg-dark-blur mr-2"
                    :move="checkMove"
                    >
                        <player-item 
                            v-for="(player,index) in lobby.tSide" 
                            :key="index" :player="player" 
                            :status="index==0?lobby.leader._id == player._id ?'Capitan | Lobby Leader':'Capitan':lobby.leader._id == player._id ?'Jugador | Lobby Leader':'Jugador'" 
                            side="tt"
                            @contextmenu.native.prevent.stop="lobbyListRightClick($event,player)"
                        ></player-item>

                </draggable>
                <draggable 
                    v-model="lobby.ctSide"
                    id="counter-terrorists"
                    group="people"
                    animation="200" 
                    @change="log"
                    @end="debounceLobbyUpdate"
                    class="flex flex-col w-1/2 flex-wrap h-sides bg-dark-blur mr-6"
                    :move="checkMove"                    
                    >
                        <player-item 
                            v-for="(player,index) in lobby.ctSide" 
                            :key="index" 
                            :player="player" 
                            :status="index==0?lobby.leader._id == player._id ?'Capitan | Lobby Leader':'Capitan':lobby.leader._id == player._id ?'Jugador | Lobby Leader':'Jugador'" 
                            side="ct"
                            @contextmenu.native.prevent.stop="lobbyListRightClick($event,player)"
                        ></player-item>
                </draggable>
            </div>
            <div class="flex justify-center">
                <button :class="{'disabled':!teamsFull}" :disabled="!teamsFull" class="bg-green-600 hover:bg-green-500 text-white focus:outline-none my-5 py-2 px-4 cursor-pointer w-40 self-center">Iniciar Mixton</button>
                <button  :class="{'disabled':letsroll}" :disabled="letsroll" @click="leaveLobby" class="bg-red-600 hover:bg-red-500 text-white focus:outline-none my-5 py-2 px-4 cursor-pointer w-16 self-center">Salir</button>

            </div>
            <p class="text-lg text-shadow text-center">{{startMessage}}</p>
        </div>

        </div> 
    </div>
    
        
</template>

<script>
import { mapState,mapMutations,mapActions } from 'vuex'
import draggable from 'vuedraggable'
import playerItem from './playerItem'
import VueSimpleContextMenu from 'vue-simple-context-menu'
import _ from 'lodash'

export default {
    name: 'lobbyControl',
    data(){
        return {
            letsroll:false,
            showLoader:false,
            connectCommand:'',
            gotIn:false,
            ready:false,
            startMessage:'Cuando los equipos esten completos se podra Iniciar el Mixton!',
            optionsGeneral: [
                {
                    name: 'Agregar como amigo',
                    slug: 'add-friend'
                }
            ],
            optionsLeader: [
                {
                    name: 'Kick',
                    slug: 'kick',
                },
                {
                    name: 'Entregar lobby',
                    slug: 'lobby-leader'
                },
                {
                    name: 'Agregar como amigo',
                    slug: 'add-friend'
                }
            ],            
            optionsLeaderForFriends: [
                {
                    name: 'Kick',
                    slug: 'kick',
                },
                {
                    name: 'Entregar lobby',
                    slug: 'lobby-leader'
                }
            ]
        }
    },
    props:[
        'mapSelected'
    ],
    methods:{
        ...mapActions([
            'fetchLobby',
            'updateLobby'
        ]),
        checkMove(evt){           
            if(this.isLobbyLeader){ //Only lobby leader can move players, for now.
                if(evt.to.childElementCount<5 || evt.to.id=='spectators'){
                    return true
                }else{
                    return false
                }
            } else{
                return false
            }           
        },
        log() {
            //console.log(this.lobby)
        },
        letsRoll(){
            let sound = document.getElementById('letsRoll')
            sound.play()
        },
        clipboardSuccessHandler(){
            let oldConnectCommand = this.connectCommand
            this.connectCommand = 'Copiado'
            setTimeout(()=>{
                this.connectCommand = oldConnectCommand
            },500)
        },
        lobbyListRightClick (event, item) {
            if(this.isLobbyLeader){                
                if(this.friends.some(friend=>friend._id == item._id)){
                    this.$refs.lobbyControlContextMenuLeaderForFriends.showMenu(event, item)
                }else if(item._id != this.me._id){
                    this.$refs.lobbyControlContextMenuLeader.showMenu(event, item)
                }
            }else{
                if(!this.friends.some(friend=>friend._id == item._id) && item._id != this.me._id){
                    this.$refs.lobbyControlContextMenuGeneral.showMenu(event, item)
                }
            }
            
            
        },
        optionClicked (event) {
            switch (event.option.slug) {
                case 'kick':
                    this.notify('Kickeando a '+event.item.name+' del lobby')
                    break
                case 'lobby-leader':
                    this.notify('Ahora '+event.item.name+' es dueño del lobby')
                    break
                case 'add-friend':
                    this.notify('Agregaste a '+event.item.name+' a tu lista de amigos')
                    break
                case 'delete-friend':
                    this.notify('Has eliminado a '+event.item.name+' de tus amigos')
                    break
                default:
                    break
            }
        },
        debounceLobbyUpdate: _.debounce(function(){ 
            if(this.isLobbyLeader){
                this.updateLobby(this.lobby)
                if(this.teamsFull){
                    this.startMessage = 'Esperando al Lobby Leader para que Inicie el Mixton!'
                }
            }           
            
        },2000),
        async leaveLobby(){
            await this.$store.dispatch('leaveLobby')
            this.$router.push('/')
        },
        ...mapMutations({
            notify:'NOTIFY',
            setMapLobby:'SET_MAP_LOBBY'
        })
    },
    mounted(){
    },
    components:{
        draggable,
        playerItem,
        VueSimpleContextMenu
    },
    watch:{
        mapSelected:function(){
           this.setMapLobby(this.mapSelected)
           this.debounceLobbyUpdate()
        }
    },
    computed:{
        ...mapState({
            me:'me',            
            friends:'friends',
            lobby:'lobby',
            teamsFull(state){
                return state.lobby.ctSide.length == 5 && state.lobby.tSide.length == 5
            },
            isLobbyLeader(state){
                return state.lobby.leader._id == state.me._id
            }
        })
    }
    
}
</script>
