import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VuexPersistence from 'vuex-persist'

Vue.use(Vuex)

const vuexLocal = new VuexPersistence({
  reducer: (state) => ({
    notifications: state.notifications,
  })
})

export default new Vuex.Store({
  state: {
    me:null,
    friends:[],
    players:[],
    lobbies:[],
    lobby:null,
    notifications:[],
    isConnected:false,
    isLoading:{
      show:false,
      message:''
    }
  },
  mutations: {
    SET_CURRENT_PLAYER: (state, player) => (state.me = player),
    SET_PLAYERS: (state, players) => (state.players = players),
    SET_FRIENDS: (state, friends) => (state.friends = friends),
    SET_LOBBIES: (state, lobbies) => (state.lobbies = lobbies),
    SET_MAP_LOBBY: (state, map) => (state.lobby.mapSelected = map),
    SET_LOBBY: (state, lobby) =>(state.lobby = lobby),
    CLEAR_NOTIFICATIONS:(state) =>(state.notifications = []),
    NOTIFY:(state, notification) => {
      let now = new Date
      now = now.toTimeString().split(' ')[0]
      now = now.split(':')
      state.notifications.push({
        timestamp:now[0]+':'+now[1],
        notification:notification
      })
    },
    SET_LOADER:(state,isLoading,message='')=>{
      state.isLoading.show = isLoading
      state.isLoading.message = message
    },
    SOCKET_CONNECT:(state)=>{state.isConnected=true},
    SOCKET_DISCONNECT:(state)=>{state.isConnected=false}
  },
  actions: {
    async checkAuth({ commit }) {
      const res = await axios.get('/auth/check')
      commit('SET_CURRENT_PLAYER', res.data)
      return res
    },
    async logout({ commit }) {
      let res = await axios.get('/auth/logout')
      commit('SET_CURRENT_PLAYER', null)
      commit('SET_LOBBY',null)
      commit('SET_LOBBIES',[])
      commit('SET_FRIENDS',[])
      commit('CLEAR_NOTIFICATIONS')
      return res
    },
    async fetchPlayers({ commit }) {
        const res = await axios.get('/api/getPlayers')
        commit('SET_PLAYERS', res.data)
    },
    async fetchFriends({ commit }) {
        const res = await axios.get('/api/getFriends')
        commit('SET_FRIENDS', res.data)
    },
    async fetchLobbies({ commit }) {
        commit('SET_LOADER',true,'Cargando lista de Mixtones')
        const res = await axios.get('/api/getPublicLobbies')
        commit('SET_LOBBIES', res.data)
        commit('SET_LOADER',false)
        
    },
    async fetchLobby({commit}) {
        commit('SET_LOADER',true,'Recuperando información del Lobby')
        const res = await axios.get('/api/getLobby')
        commit('SET_LOBBY', res.data)
        commit('SET_LOADER',false)   
    },
    async updateLobby({commit},lobby){
      commit('SET_LOADER',true,'Actualizando información del Lobby')
      const res = await axios.post('/api/updateLobby',lobby)
      commit('SET_LOBBY',res.data)
      commit('SET_LOADER',false)
    },
    async createLobby({commit}) {
      commit('SET_LOADER',true, 'Creando Lobby para Mixton')
      const res = await axios.get('/api/createLobby')
      commit('SET_LOBBY', res.data)
      commit('SET_LOADER',false) 
    },
    async joinLobby({commit},code){
      commit('SET_LOADER',true,'Estás entrando al Lobby')
      const res = await axios.get('/api/joinLobby/'+code)
      commit('SET_LOBBY',res.data)
      commit('SET_LOADER',false)

    },
    async leaveLobby({commit}) {
      commit('SET_LOADER',true, 'Estás saliendo del Lobby')
      commit('NOTIFY','Saliendo del lobby '+this.state.lobby.code)
      await axios.get('/api/leaveLobby')
      commit('SET_LOBBY', null)
      commit('SET_LOADER',false)
      
    }
  },
  modules: {
  },
  plugins: [vuexLocal.plugin]
})
