<template>
    <div 
    class="h-32 w-32 bg-100 bg-center hover:filter-none cursor-pointer hover:shadow-glow hover:bg-zoom transition-backgroud-size duration-200" 
    :style="{ backgroundImage: `url('/images/maps/${this.map}.jpg')`}"
    :class="{'filter-none shadow-glow bg-zoom':this.map == this.activeMap,'filter-grayscale':this.map != this.activeMap}"
    @click="updateActiveMap" >
    </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
    name: 'mapItem',
    methods:{
        updateActiveMap(){
            if(this.isLobbyLeader){
                this.$emit('onUpdateMap',this.map)
            }            
        }
    },
    model:{
        prop:'activeMap',
        event:'onUpdateMap'
    },
    props:[
        'map',
        'activeMap'
    ],
    computed:{
        ...mapState({
            isLobbyLeader(state){
                return state.lobby.leader._id == state.me._id
            }
        })
    }
}
</script>