require('dotenv').config()
import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import Clipboard from 'v-clipboard'
import VueSocketIOExt from 'vue-socket.io-extended'
import VTooltip from 'v-tooltip'
import io from 'socket.io-client'

var socketUrl = process.env.BASE_URL

if(process.env.NODE_ENV == 'development'){
  socketUrl = process.env.VUE_APP_IO
}

const socket = io(socketUrl,{
  autoConnect: false
})

Vue.use(VTooltip)
Vue.use(Clipboard)
Vue.use(VueSocketIOExt, socket, {store})

import '@/assets/scss/main.scss'
import '@/assets/js/helpers'

Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) { 
    store.dispatch('checkAuth').then(()=>{
      if (store.state.me) {
        socket.open()
        if(to.name == 'Lobby'){
          store.dispatch('fetchLobby').then(async()=>{
            if(!store.state.lobby){
              await store.dispatch('createLobby')
                next()
            }
            next()
          })  
        }else if(to.name == 'JoinLobby'){
          store.dispatch('joinLobby',to.params.id).then(()=>{
            next({
              path: '/lobby'
            })
          }) 
        }else if(to.name == 'Home'){
          store.dispatch('fetchLobbies').then(()=>{
            if(!store.state.lobby){
              store.dispatch('fetchLobby').then(()=>{
                next()
              })
            }
            next()
          })  
        } else{
          next()
        }
      } else { 
        socket.close()     
        next({
          path: '/login'
        })
      }
    })    
  } else if(to.matched.some(record => record.meta.checkLogin)) {  
    store.dispatch('checkAuth').then(()=>{
      if (store.state.me) {
        socket.open()
        next({
          path: '/'
        })
      } else {
        socket.close()
        next()
      }
    })    
  } else if(to.matched.some(record => record.meta.checkLogout)) {
    store.dispatch('logout').then(()=>{
      socket.close()
      next({
        path: '/login'
      })
    })
  } else {
    next() 
  }
})

socket.on("update-lobbies-list",function() {
  store.dispatch('fetchLobbies')
})

socket.on('update-lobby',function(data){
  if(store.state.lobby._id == data){
    store.dispatch('fetchLobby')
  }  
})


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
