<template>
    <div class="flex flex-row py-2 hover:bg-light-blur cursor-pointer border-transparent border-solid border-l-2 hover:border-white">
        <img :src="player.steamInfo.avatarmedium" alt="" 
        class="h-12 shadow-sm border-white border-l-4 ml-4" 
        :class="{'border-red-700':player.status==='Desconectado',        
        'border-blue-700': this.side=='ct',
        'border-yellow-600':this.side=='tt',
        'border-green-700':player.status==='Conectado'&&!this.side,
        }">
        <p class="ml-5">
            {{player.name}}<br/>
            {{this.status}}
        </p>
        
    </div>
</template>

<script>
export default {
    name: 'PlayerItem',
    props:{
        player:Object,
        status:String,
        side:String
    }
}
</script>