<template>
    <div ref="friendsList" class="flex flex-col overflow-auto scrollbar">
        <vue-simple-context-menu
            :element-id="'friendsList'"
            :options="optionsArray.filter((option)=>option.slug!='invite-lobby'||lobby)"
            :ref="'friendListContextMenu'"
            @option-clicked="optionClicked"
        >
        </vue-simple-context-menu>
        <player-item v-for="friend in friends" 
        :key="friend.id" 
        :player="friend"
        :status="friend.status"
        @contextmenu.native.prevent.stop="friendListRightClick($event,friend)"
        ></player-item>
        <p v-if="friends.length == 0" class="text-center mt-3">Comienza agregando amigos</p>
        
    </div>
</template>

<script>
import playerItem from './playerItem'
import VueSimpleContextMenu from 'vue-simple-context-menu'
import { mapState,mapMutations,mapActions } from 'vuex'

export default {
    name: 'Friends',
    data(){
        return {
            optionsArray: [
                {
                    name: 'Ver Perfil',
                    slug: 'view-profile'
                },
                {
                    name: 'Invitar al Lobby',
                    slug: 'invite-lobby',
                },                
                {
                    name: 'Eliminar',
                    slug: 'delete-friend'
                }
            ]
        }
    },
    computed: mapState([
        'friends',
        'lobby'
    ]),
    components:{
        playerItem,
        VueSimpleContextMenu
    },
    mounted(){
        this.getFriends()
    },
    methods:{
        friendListRightClick (event, item) {
                let left = window.innerWidth - this.$refs.friendsList.clientWidth

                Object.defineProperty(event, 'pageX', {
                    value: event.pageX - left,
                    writable: true
                })
            this.$refs.friendListContextMenu.showMenu(event, item)
        },
        optionClicked (event) {
            switch (event.option.slug) {
                case 'invite-lobby':
                    this.notify('Has invitado a '+event.item.name)
                    break
                case 'delete-friend':
                    this.notify('Has eliminado a '+event.item.name+' de tus amigos')
                    break
                case 'view-profile':
                    window.open(event.item.steamInfo.profileurl,'_blank')
                    break
                default:
                    break
            }
        },        
        ...mapMutations({
            notify:'NOTIFY'
        }),
        ...mapActions({
            getFriends:'fetchFriends'
        })
    }
}
</script>
