<template>
  <div id="app" class="" >
      <not-found v-if="this.$router.currentRoute.name=='NotFound'"/>
      <login v-else-if="this.$router.currentRoute.name=='Login'"/>   
      <div class="flex h-screen overflow-hidden bg-local bg-center bg-cover" style="background-image: url('/images/armada-back.jpg')" v-else>

          <notifications/>

          <div class="flex flex-col w-full">
            <navbar/>
            <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode="out-in" :duration="200">
              <router-view></router-view>
            </transition>
            
          </div>

          <div class="flex flex-col w-390px shadow-left bg-blur backdrop-blur">
            <profile/>
            <div class="h-px bg-gray-500 shadow-sm mx-2 my-1"></div>
            <friends/> 
          </div>

      </div>   
      
  </div>
</template>

<script>
import Login from './views/Login'
import NotFound from './views/404.vue'
import Navbar from './components/Navbar'
import Friends from './components/Friends'
import Profile from './components/Profile'
import Notifications from './components/Notifications'

export default {
  components:{
    Navbar,
    Friends,
    Profile,
    Notifications,
    Login,
    NotFound
  },
  mounted(){
    if(!this.$store.state.notifications.length && this.$store.state.me){
      this.$store.commit('NOTIFY','Bienvenido '+this.$store.state.me.name)
    }    
  }
}
</script>